import React, { useState } from 'react';
import { HashRouter, Switch, Route, Link } from "react-router-dom";
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestaurantMenuOutlinedIcon from '@material-ui/icons/RestaurantMenuOutlined';
import DirectionsWalkOutlinedIcon from '@material-ui/icons/DirectionsWalkOutlined';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import { makeStyles } from '@material-ui/core/styles';

import RandomMenu from './pages/RandomMenu';
import Eatout from './pages/Eatout';
import Godocs from './pages/Godocs';

const useStyles = makeStyles({
  root: {
    position: "fixed",
    bottom: 0,
    width: "100%",
  },
  content: {
    paddingBottom: "40px !important",
  },
});

const AppsBottomNavigation = () => {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  return (
    <BottomNavigation
      value={value}
      onChange={(event, newValue) => {
        setValue(newValue);
      }}
      showLabels
      className={classes.root}
    >
      <BottomNavigationAction component={Link} to="/" label="メニュー生成" icon={<RestaurantMenuOutlinedIcon />} />
      <BottomNavigationAction component={Link} to="/eatout" label="外食一覧" icon={<DirectionsWalkOutlinedIcon />} />
      <BottomNavigationAction component={Link} to="/godocs" label="カテゴリから探す" icon={<DescriptionOutlinedIcon />} />
    </BottomNavigation>
  );
};

const App = () => {
  const classes = useStyles();
  return (
    <HashRouter>
      <div className={classes.content}>
        <Switch >
          <Route exact path="/" component={RandomMenu} />
          <Route path="/eatout" component={Eatout} />
          <Route path="/godocs" component={Godocs} />
        </Switch>
      </div>
      <AppsBottomNavigation />
    </HashRouter>
  );
}

export default App;
