import React, { useState, useEffect } from 'react';
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import SwipeableViews from "react-swipeable-views";
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Container from "@material-ui/core/Container";
import axios from 'axios';
import { isAndroid, isIOS } from "react-device-detect";
import Fab from '@material-ui/core/Fab';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(() => ({
  swipeviews: {
    marginTop: 15,
  },
  root: {
    flexGrow: 1,
    position: "fixed",
    top: 0,
    width: "100%",
    zIndex: 100,
  },
  extendedIcon: {
    margin: 0,
    top: 'auto',
    right: 20,
    bottom: 70,
    left: 'auto',
    position: 'fixed',
    zIndex: 100,
  },
  tableprintL: {
    display: 'inline-block'
  },
  tableprintR: {
    display: 'inline-block',
    float: 'right'
  }
}));

const CenteredTabs = (props) => {
  return (
    <Paper >
      <Tabs
        value={props.value}
        onChange={props.handleChange}
        indicatorColor="primary"
        textColor="primary"
        centered
      >
        <Tab label="メイン" />
        <Tab label="サラダ" />
        <Tab label="スープ" />
        <Tab label="1品" />
      </Tabs>
    </Paper>
  );
}
CenteredTabs.propTypes = {
  value: PropTypes.number,
  handleChange: PropTypes.func
};

const TestByValue = (props) => {
  const classes = useStyles();

  const date_diff = (date) => {
    const today = new Date();
    const d = new Date(date);
    const diff_time = today.getTime() - d.getTime();
    const diff_in_days = diff_time / (1000 * 60 * 60 * 24);
    return Math.round(diff_in_days);
  }
  const data = props.data
  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableBody>
          {data.map((row, idx) => (
            <TableRow key={idx}>
              <TableCell component="th" scope="row">
                <div >
                  <Typography variant="h6" className={classes.tableprintL}>
                    {row.name}
                  </Typography>
                  <Typography variant="subtitle2" className={classes.tableprintR}>
                    （{date_diff(row.date)}日前）
                  </Typography>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>);
}
TestByValue.propTypes = {
  data: PropTypes.array
};

const randgen = () => {
  return Math.floor(Math.random() * Math.floor(365));
}

const shuffled_arr = (arr) => {
  const shuffle_t = 1000;
  for (let t = 0; t < shuffle_t; ++t) {
    const i = randgen() % arr.length;
    const j = randgen() % arr.length;
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}


const filterd_after14days = (arr) => {
  return arr.filter(item => {
    const date = new Date(item["date"]);
    date.setDate(date.getDate() + 14);
    const today = new Date();
    return date < today;
  });
};

const UniqByLatest = (arr) => {
  const knownElements = new Map();
  for (const elem of arr) {
    const key = elem["name"];
    if (knownElements.has(key)) {
      const latest = knownElements.get(key);
      // comp latest, elem
      const day_map = new Date(latest["date"]);
      const day_elem = new Date(elem["date"]);
      if (day_elem > day_map) {
        knownElements.set(key, elem);
      }
    } else {
      knownElements.set(key, elem);
    }
  }
  const res = [];
  for (let value of knownElements.values()) {
    res.push(value);
  }
  return res;
}

const flatten_and_add_category = (users) => {
  const arr = [];
  users.forEach(item => {
    if (item.main) {
      arr.push({ 'category': 'main', 'name': item.main, 'date': item.date });
    }
    if (item.sub1) {
      arr.push({ 'category': 'sub', 'name': item.sub1, 'date': item.date });
    }
    if (item.sub2) {
      arr.push({ 'category': 'sub', 'name': item.sub2, 'date': item.date });
    }
    if (item.sub3) {
      arr.push({ 'category': 'sub', 'name': item.sub3, 'date': item.date });
    }
  });
  return arr;
};

const f = (value) => {
  if (value == 0) {
    return { "category": "main", "word": [] };
  } else if (value == 1) {
    return { "category": "", "word": ['サラダ'] };
  } else if (value == 2) {
    return { "category": "", "word": ['スープ', '汁'] };
  } else if (value == 3) {
    return { "category": "sub", "word": [] };
  }
}

const hoge = (data, cond) => {
  const category = cond["category"];
  if (category === "main") {
    return data.filter(item => item['category'] === category);
  } else if (category === "sub") {
    const subs = data.filter(item => item['category'] === category);
    return subs.filter(item => {
      let ok = true;
      for (let word of ["スープ", "汁", "サラダ"]) {
        if (item["name"].includes(word)) {
          ok = false;
        }
      }
      return ok;
    });
  } else {
    const words = cond["word"];
    return data.filter(item => {
      for (let word of words) {
        if (item["name"].includes(word)) {
          return true;
        }
      }
      return false;
    });
  }
}

const CreateData = () => {
  let [menus, setMenus] = useState([]);
  useEffect(async () => {
    const response = await axios("https://raw.githubusercontent.com/Yang-33/MyGitHubPages/master/Gohan/homemenulist.json",);
    setMenus(response.data);
  }, []);

  if (menus.length) {
    const datas = flatten_and_add_category(menus);
    const latest_datas = UniqByLatest(datas);
    const filtered_latest_datas = filterd_after14days(latest_datas);
    const filtered_menus = shuffled_arr(filtered_latest_datas);
    const unchi = [0, 1, 2, 3].map((item, idx) =>
      <TestByValue key={idx} data={hoge(filtered_menus, f(item)).slice(0, 30)} />);
    return unchi;
  }

  const a = [0].map((item, idx) =>
    <div key={idx}></div>)

  return a;
}

const Alllist = (props) => {
  const classes = useStyles();
  const data = props.data;
  return (
    <SwipeableViews index={props.index} onChangeIndex={props.handleChangeIndex} className={classes.swipeviews} animateHeight={data.length !== 1} enableMouseEvents >
      { data}
    </SwipeableViews>
  );
}

Alllist.propTypes = {
  data: PropTypes.array,
  index: PropTypes.number,
  handleChangeIndex: PropTypes.func
};


const url_from_device = () => {
  if (isAndroid) {
    return "intent://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit#Intent;scheme=https;package=com.google.android.apps.docs.editors.docs;S.browser_fallback_url=market://details?id=com.google.android.apps.docs.editors.docs;end";
  } else if (isIOS) {
    return "googledocs://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit";
  } else {
    return "https://docs.google.com/document/d/1RTqNDl6ZnNMOAc_n_MLn0Z9Kiwvc7UaWHbpjwoRSWJg/edit"
  }
};

const DocFab = () => {
  const classes = useStyles();
  const DOC_URL = url_from_device();
  return (
    <div className={classes.extendedIcon}>
      <Fab color="primary" variant="extended" href={DOC_URL} target="_blank" rel="noopener noreferrer">
        <DescriptionOutlinedIcon />
          Open Docs
      </Fab>
    </div>
  );
}

const Godocs = () => {
  const classes = useStyles();

  const [index, setIndex] = useState(0);
  const handleChange = (event, newValue) => {
    setIndex(newValue);
  }
  const handleChangeIndex = (index) => {
    setIndex(index);
  }

  const datas4 = CreateData();

  return (
    <Container>
      <DocFab />
      <CenteredTabs value={index} handleChange={handleChange} className={classes.root} />
      <Alllist index={index} handleChangeIndex={handleChangeIndex} data={datas4} />
    </Container>
  );
}

export default Godocs;
