import React, { Component, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import CachedIcon from '@material-ui/icons/Cached';
import axios from 'axios';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

const MyCard = (props) => {
  const data = props.data;
  const cardStyle = {
    minHeight: 160,
  };
  return (
    <Card style={cardStyle}>
      <CardContent>
        <Typography variant="h5" component="h3">
          {data.main}
        </Typography>
        <Typography color="textSecondary">
          {data.sub1}
        </Typography>
        <Typography color="textSecondary">
          {data.sub2}
        </Typography>
        <Typography color="textSecondary">
          {data.sub3}
        </Typography>
      </CardContent>
    </Card>
  );
}
MyCard.propTypes = {
  data: PropTypes.object.isRequired
};

// json array
const filterd_after14days = (arr) => {
  return arr.filter(item => {
    const date = new Date(item["date"]);
    date.setDate(date.getDate() + 14);
    const today = new Date();
    return date < today;
  });
};

const RandList = (props) => {
  let [users, setUsers] = useState([]);
  useEffect(async () => {
    const response = await axios("https://yang33-kassa.jp/Gohan/homemenulist.json",);
    setUsers(response.data);
  }, []);

  const selected_datas = [];
  if (users.length) {
    const filterd_datas = filterd_after14days(users);
    for (let i = 0; i < props.take_t; ++i) {
      const data = filterd_datas[props.rands[i] % filterd_datas.length];
      selected_datas.push(data);
    }
  }

  const RandListsyle = {
    marginTop: "-15px"
  };

  const cards = (
    <Grid container spacing={4} style={RandListsyle}>
      {selected_datas.map((item, idx) => {
        return (
          <Grid item xs={12} key={idx}>
            <MyCard data={item} key={idx} />
          </Grid>
        );
      })}
    </Grid>
  );
  return (
    cards
  );
};

RandList.propTypes = {
  rands: PropTypes.array.isRequired
};

const randgen = () => {
  return Math.floor(Math.random() * Math.floor(365));
}

const shuffled_arr = () => {
  const arr = Array.from(Array(365).keys());
  const shuffle_t = 1000;
  for (let t = 0; t < shuffle_t; ++t) {
    const i = randgen() % arr.length;
    const j = randgen() % arr.length;
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}

class RandomMenu extends Component {
  constructor(props) {
    super(props);
    const arr = shuffled_arr();
    this.state = {
      rands: arr
    }
    this.handleClickButton = this.handleClickButton.bind(this);
  }

  handleClickButton() {
    const new_rands = shuffled_arr();
    this.setState(
      { rands: new_rands }
    );
  }

  render() {
    const Fabstyle = {
      position: 'absolute',
      bottom: "70px",
      right: "20px",
    };

    const Tabstyle = {
      flexGrow: 1,
      position: "fixed",
      top: 0,
      width: "100%",
    }

    const CenteredTabs = () => {
      return (
        <Paper>
          <Tabs
            indicatorColor="primary"
            textColor="primary"
            centered
            value={0}
          >
            <Tab label="今日の3メニュー" />
          </Tabs>
        </Paper>
      );
    }

    return (
      <Container>
        <CenteredTabs style={Tabstyle} />
        <RandList rands={this.state.rands} take_t={3} />
        <Fab color="primary" aria-label="add" onClick={this.handleClickButton} style={Fabstyle}>
          <CachedIcon />
        </Fab>
      </Container>
    )
  }
}

export default RandomMenu;
